:root{
  --fontFamily-system: system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    --fontFamily-serif: Rockwell,Bookman,"Bookman Old Style",georgia,times,serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    --fontFamily-mono: "Noto Mono","Roboto Mono","Source Code Pro","Droid Sans Mono","DejaVu Sans Mono",Monaco,"Lucida Console",monospace,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    --color-brandRed: #b12525;
    --color-strongRed: #ec0909;
    --color-cta: #387000;
    --color-grey15: #262626;
    --color-grey20: #333;
    --color-grey90: #e6e6e6;
    --color-grey95: #f2f2f2;
    --color-white: #fff;
    --color-border: rgba(0,0,0,0.3);
    --color-borderLight: rgba(0,0,0,0.15);
    --color-grey80: #ccc;
    --color-grey60: #999;
    --color-grey40: #666;
    --color-paleRed: #e34a52;
    --color-danger: var(--color-brandRed);
    --color-dangerDark: #570f0f;
    --color-dangerLight: #f7d4d4;
    --color-warning: #d4b668;
    --color-warningDark: #7f6524;
    --color-warningLight: #f2e8ce;
    --color-success: #59a666;
    --color-successDark: #36633d;
    --color-successLight: #deede0;
    --color-graph1: #a65959;
    --color-graph2: #a69959;
    --color-graph3: #59a666;
    --color-graph4: #598ca6;
    --color-graph5: #8c59a6;
    --color-graphMono1: #295200;
    --color-graphMono2: #349344;
    --color-graphMono3: #aad4b1;
    --card-shadow: 0 2px 3px 0 rgba(0,0,0,0.2);
    --float-shadow: 0 3px 10px 0 rgba(0,0,0,0.3);
}
html{
  min-height: 100%;
}
html,body{  
  font-family: var(--fontFamily-system);
  color: var(--color-grey20);
  font-size: 14px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}
*, :after, :before {
  box-sizing: border-box;
}